import { noop } from 'lodash-es';
import React from 'react';

interface Rect {
  top: number;
  left?: number;
}

export const ScrollableContainerRect =
  React.createContext<[Rect, Function]>([{ top: 0 }, noop]);

export const useScrollableContainerRect = () => {
  return React.useContext(ScrollableContainerRect);
};
